<template>
  <div>
    <section v-if="errord">
      <p>We're sorry, we're not able to retrieve this information at the moment, please try back later</p>
    </section>
    <section
      v-else
      class="container-fluid wrap"
    >
      <div v-if="loading">Loading...</div>
      <div v-else>
        <div class="mt-1">
          <div class="lead">
            {{ apod.title }}
          </div>
          <div>
            {{ apod.date }}
          </div>
        </div>
        <div>
          <img
            :src="apod.url"
            :data-image-opened="apod.url"
            class="figure-img img-fluid mt-2 rounded"
            alt="x"
          >
        </div>

        <div class="mt-1">
          <button
            v-on:click="speakEng"
            type="button"
            value="Play"
            class="btn btn-dark btn-sm btn-block"
          >
            Play
          </button>
          <textarea-autosize
            v-model="apod.explanation"
            v-cloak
            readonly
            id="text"
            class="mt-2 text-selection"
          ></textarea-autosize>
        </div>

      </div>
    </section>

    <hr>
    <p class="text-center">
      <em>made with vue & nasa's api</em><br>
      <em>(2019)</em>
    </p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'APOD',
  data() {
    return {
      apod: null,
      loading: true,
      errord: false
    }
  },
  mounted() {
    axios.get('https://api.nasa.gov/planetary/apod?api_key=vf64R8zpJBXhv3IIw4argAXnALVdrDRLyNzts8lb').then(response => (this.apod = response.data))
      .catch(error => {
        // eslint-disable-next-line no-console
        console.log(error)
        this.errord = true
      })
      .finally(() => this.loading = false)
  },
  methods: {
    speakEng() {
      const u = new SpeechSynthesisUtterance();
      u.text = this.apod.explanation;
      u.lang = 'en-US';
      u.voiceURI = "native";
      u.rate = 1.35;
      u.pitch = 0.1;
      u.onend = speechSynthesis.speak(u);
    }
  }
}
</script>

